<template>
  <div class="getMedicineRecord" style="max-width: 100%;box-sizing: border-box">
    <myHeader
      :isBack="true"
      backPath="/getMedicine"
      title="记录列表">
    </myHeader>
    <div class="seat-head"></div>
    
    <div class="filter-block">
      <div @click="selectFilterDate" class="left">
        <span class="filter-item">日期筛选</span>
        <span>▼</span>
      </div>
      <div class="right">
        <span @click="selectFilterStatus" class="filter-item">状态筛选</span>
        <span>▼</span>
      </div>
    </div>
    
    <div class="list-container">
      <div :style="{ height: wrapperHeight + 'px' }"
           class="page-loadmore-wrapper"
           ref="wrapper"
           v-show="recordList.length">
        <mt-loadmore :auto-fill="false"
                     :bottom-all-loaded="allLoaded"
                     :bottom-method="loadBottom"
                     @bottom-status-change="handleBottomChange" ref="loadmore">
          
          <RecordList
            :listData="recordList"
            @on-handlerBtn="handlerBtn"
            @on-handlerIcon="handlerIcon"
            @on-handlerLeft="handlerLeft">
          </RecordList>
        
        </mt-loadmore>
      </div>
    </div>
    
    <filter-time @on-filter-search="onTimeChangeFilter"
                 ref="filterTime"></filter-time>
    
    <!--选择筛选状态-->
    <div class="my-prop">
      <mt-popup
        position="bottom"
        v-model="switchSelectStatusPopup">
        <mt-picker :show-toolbar="true"
                   :slots="statusTypeSlots" ref="picker" value-key="name">
          <span @click="handleConfirm" class="sure-btn">确认</span>
          <span @click="handleReset" class="reset-btn">重置筛选</span>
        </mt-picker>
      </mt-popup>
    </div>
    
    <NoRecord v-if="!recordList.length"></NoRecord>
  </div>
</template>

<script>
  import myHeader from "@/components/Header"
  import filterTime from '@/components/FilterTime'
  import RecordList from "@/components/RecordList"
  import NoRecord from '@/components/NoRecord'
  import {getDrugList, deleteDrug} from '@/api/materialApply'
  import appConfig from '@/config/appConfig'
  import { Indicator } from 'mint-ui'
  
  export default {
    name: "getMedicineRecord",
    data() {
      return {
        allLoaded: false,
        bottomStatus: '',
        pageNumber: 1,
        pageSize: 10,
        recordList: [{
          recordTitle: '',
          recordTime: '',
          recordState: '',
          rcordId: '',
          billStatusIndex: ''
        }],
        endtime: '',
        starttime: '',
        state: '',
        wrapperHeight: 0,
        switchSelectStatusPopup: false,
        statusTypeSlots: [{
          values: [
            {name: '进行中', id: 1},
            {name: '已完成', id: 2},
            {name: '已取消', id: 3},
          ],
          textAlign: 'center'
        }],
      };
    },
    components: {
      RecordList,
      myHeader,
      NoRecord,
      filterTime
    },
    created() {
      this._getDrugList({
        endtime: this.endtime,
        starttime: this.starttime,
        state: this.state,
        pageNumber: 1,
        pageSize: this.pageSize
      });
    },
    mounted() {
      this.$nextTick(() => {
        this.wrapperHeight = document.documentElement.clientHeight - this.$refs.wrapper.getBoundingClientRect().top;
      });
    },
    methods: {
      handlerLeft(rcordId) {
        this.gotoDetail(rcordId);
      },
      handlerIcon(rcordId) {
        this.gotoDetail(rcordId);
      },
      handlerBtn(rcordId) {
        console.log(rcordId);
        this.cancelApply(rcordId);
      },
      gotoDetail(rcordId) {
        // this.$router.push({name: 'getMedicineRecordDetail', params: {rcordId}});
        this.$router.push('/getMedicineRecordDetail?rcordId='+rcordId);
      },
      _getDrugList(data) {
        Indicator.open({
          text: '加载中...',
          spinnerType: 'fading-circle'
        });
        getDrugList(data).then((res) => {
          if (res.data) {
            let newList = res.data.list.map((item, index) => {
              return {
                recordTitle: item.number,
                recordTime: item.applydate,
                recordState: appConfig.recordState[item.state],
                rcordId: item.rcordId,
                billStatusIndex: item.billStatusIndex
              }
            });
            this.recordList = newList;
            if (this.recordList.length < this.pageSize) {
              this.allLoaded = true;
            }
          } else {
            this.recordList = [];
          }
          Indicator.close();
        }, (err) => {
          Indicator.close();
        });
      },
      selectFilterDate() {
        this.$refs.filterTime.selectFilterDate();
      },
      onTimeChangeFilter({starttime, endtime}) {
        this._getDrugList({
          endtime: endtime,
          starttime: starttime,
          pageNumber: 1,
          pageSize: this.pageSize,
          state: this.state,
        });
      },
      handleConfirm() {
        this.state = this.$refs.picker.getValues()[0].id;
        this._getDrugList({
          endtime: this.endtime,
          starttime: this.starttime,
          pageNumber: 1,
          pageSize: this.pageSize,
          state: this.state,
        });
        
        this.switchSelectStatusPopup = false;
      },
      handleReset() {
        this.state = '';
        this.starttime = '';
        this.endtime = '';
        
        this._getfeedRecordList({
          endtime: this.endtime,
          starttime: this.starttime,
          pageNumber: 1,
          pageSize: this.pageSize,
          state: this.state,
        });
        
        this.switchSelectStatusPopup = false;
      },
      selectFilterStatus() {
        this.switchSelectStatusPopup = !this.switchSelectStatusPopup;
      },
      handleBottomChange(status) {
        this.bottomStatus = status;
      },
      loadBottom() {
        console.log('下来');
        this.pageNumber++;
        getDrugList({
          endtime: this.endtime,
          starttime: this.starttime,
          pageNumber: this.pageNumber,
          pageSize: this.pageSize,
          state: this.state,
        }).then((res) => {
          let newList = res.data.list.map((item, index) => {
            return {
              recordTitle: item.number,
              recordTime: item.applydate,
              recordState: appConfig.recordState[item.state],
              rcordId: item.rcordId,
              billStatusIndex: item.billStatusIndex
            }
          });
  
          if (newList.length < this.pageSize) {
            this.allLoaded = true;
          } else {
            this.allLoaded = false;
          }
  
          this.recordList = [...this.recordList, ...newList];
          
          this.$refs.loadmore.onBottomLoaded();
          
        }, (err) => {
          this.allLoaded = true;
        });
      },
      cancelApply(rcordId) {
        this.$messagebox.confirm('确定执行此操作?').then(action => {
          
          deleteDrug(rcordId).then(() => {
            this.$toast({
              message: '取消成功！',
              duration: 1000
            })
          }, () => {
          
          });
  
          this._getDrugList({
            endtime: this.endtime,
            starttime: this.starttime,
            state: this.state,
            pageNumber: 1,
            pageSize: this.pageSize
          });
          
        });
      }
    }
  };
</script>

<style lang="scss" scoped>
  .getMedicineRecord {
    min-height: 100vh;
    background-color: #f8f8f8;
    font-size: .16rem;
    
    .head {
      background-color: #fff;
    }
    
    .filter-block {
      margin-top: .1rem;
      display: flex;
      justify-content: space-between;
      align-items: center;
      height: .44rem;
      padding: 0 .1rem;
      border-bottom: 1px solid #ddd;
      background: #fff;
      font-size: .14rem;
      color: #888;
      
      .left {
        width: 50%;
      }
      
      .right {
        width: 50%;
        text-align: right;
      }
    }
    
    .page-loadmore-wrapper {
      overflow: scroll
    }
    
  }
  
  .my-prop {
    .mint-popup {
      width: 100%;
      height: 25%;
    }
    
    /deep/ .picker-toolbar {
      height: 0;
    }
    
    .reset-btn {
      position: absolute;
      top: .05rem;
      left: .05rem;
      padding: .05rem .20rem;
      font-size: .16rem;
      z-index: 99;
    }
    
    .sure-btn {
      position: absolute;
      top: .05rem;
      right: .05rem;
      padding: .05rem .20rem;
      font-size: .16rem;
      z-index: 99;
    }
  }
</style>






